import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import './App.css';
import HomePage from './pages/HomePage/HomePage';
//import ProjectPage from './pages/ProjectPage/ProjectPage';
import SchoolPage from './pages/SchoolPage/SchoolPage';
import ExperiencePage from "./pages/ExperiencePage/ExperiencePage";
import ASA from "./pages/ASA/ASA";
import NorCalUVSA from "./pages/NorCalUVSA/NCUVSA";
import Jade from "./pages/Jade/Jade";
import NotFoundPage from "./pages/404/NotFoundPage";

import ProjectPageCopy from './pages/ProjectPage/ProjectPageCopy';

function App() {
  return (
    // Routes for website
    <Router>
      <Switch>
        <Route exact path="/">
          <HomePage />
        </Route>
        <Route exact path="/projects">
          <ProjectPageCopy />
        </Route>
        <Route exact path="/school">
          <SchoolPage />
        </Route>
        <Route exact path="/experience">
          <ExperiencePage />
        </Route>
        <Route exact path="/asa">
          <ASA />
        </Route>
        <Route exact path="/norcaluvsa">
          <NorCalUVSA />
        </Route>
        <Route exact path="/jade">
          <Jade />
        </Route>
        {/*keep at very end*/}
        <Route path="*" component={NotFoundPage} />
      </Switch>
    </Router>
  );
}

export default App;
